import React, { useState, useRef, useEffect } from 'react';
import styled from 'styled-components';
import { Hamburger } from '../HumberButton';
import {
  animated,
  useTransition,
  useSpring,
  useChain,
  config
} from 'react-spring';
// import 'normalize-css';

const Button = styled(Hamburger)`
  position: fixed;
  top: 30px;
  right: 30px;
  font-size: inherit;
`;
const Nav = styled(animated.nav)`
  position: fixed;
  background: var(--primary);
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  overflow-y: auto;
  transform-origin: 100% 50%;
  right: 0px;
  width: 100%;
  left: auto;

  ul {
    margin: 0;
    padding: 0;
  }
`;

const Li = styled(animated.li)`
  list-style: none;

  a {
    color: #fff;
    font-size: 50px;
    font-weight: bold;
    text-transform: uppercase;
    text-decoration: none;
    line-height: 1.3;
  }
`;

const menuItems = [
  { name: 'Home', link: '/' },
  { name: 'About', link: '/about/' },
  { name: 'Portfolio', link: '/about/' },
  { name: 'Skills', link: '/contact/' },
  { name: 'Contact', link: '/contact/' }
];

const Container = styled.div`
  position: relative;
  z-index: 1400;
`;

const NavHeadline = styled.div`
  position: absolute;
  top: 0;
  width: 100%;
`;
const NavHeadlineIntro = styled(animated.div)`
  display: flex;
  justify-content: center;
  align-items: center;

  height: 200px;
  width: 100%;
  font-size: 2rem;
  color: white;
  background-color: var(--primary);
`;

export const SideNav = ({ className }) => {
  const [showMenu, setShowMenu] = useState(false);

  React.useEffect(() => {
    if (showMenu) document.body.style.overflow = 'hidden';
    else document.body.removeAttribute('style');
  }, [showMenu]);

  const toggleMenu = () => {
    setShowMenu(!showMenu);
  };

  // Set refs - required for useChain
  const navRef = useRef();
  const liRef = useRef();

  // Setup animation for nav element
  const springProps = useSpring({
    ref: navRef,
    config: config.default,
    from: { transform: 'scaleX(0)' },
    to: {
      transform: showMenu ? 'scaleX(1)' : 'scaleX(0)'
    }
  });

  const props = useSpring({ opacity: showMenu ? 1 : 0 });

  // Setup animations for nav items
  const liTransitions = useTransition(
    showMenu ? menuItems : [],
    (item) => item.name,
    {
      ref: liRef,
      trail: 400 / menuItems.length,
      from: { opacity: 0, transform: 'translateY(20px)' },
      enter: { opacity: 1, transform: 'translateY(0)' },
      leave: { opacity: 0, transform: 'translateY(20px)' }
    }
  );

  // On showMenu, start with nav animation then nav items
  useChain(showMenu ? [navRef, liRef] : [liRef, navRef], [
    0,
    showMenu ? 0.4 : 0.6
  ]);

  return (
    <Container className={className}>
      <Nav style={springProps}>
        {/* <NavHeadline>
          <NavHeadlineIntro style={props}>Michael Luo</NavHeadlineIntro>
        </NavHeadline> */}
        <ul>
          {liTransitions.map(({ item, key, props }) => (
            <Li key={key} style={props}>
              <a href={item.link}>{item.name}</a>
            </Li>
          ))}
        </ul>
      </Nav>
      <Button open={showMenu} onClick={toggleMenu}>
        Toggle Menu
      </Button>
    </Container>
  );
};
